<template>

    <div id="stayls">
        <headerclass></headerclass>
        <div class="psliker">
            <div class="LeftSide">
                <div class="left_ff">
                    <p class="logio">众人信息</p>
                    <p class="isop" v-if='this.queryTotal'>总计成功请求： {{this.queryTotal}}条</p>
                    <p class="isop" v-else></p>
                </div>
                <div class="left_rr">
                    <button class="checkpp" @click="checkpp">查询</button>
                    <div class="block">
                        <el-date-picker
                                v-model="DataSelect"
                                type="daterange"
                                range-separator=""
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </div>
                </div>
            </div>
            <div class="Whole">
                <div class="claope">
                    <p class="fello">统计计费</p>
                    <div class="">
                        <el-table
                                :data="tableData"
                                style="width: 100%">
                            <el-table-column
                                    label="序号"
                                    width="70"
                                    type="index">
                            </el-table-column>
                            <el-table-column
                                    prop="custName"
                                    label="客户名称"
                                    width="100"
                                    >
                            </el-table-column>
                            <el-table-column
                                    prop="custNumber"
                                    label="客户编号">
                            </el-table-column>
                            <el-table-column
                                    prop="requestDate"
                                    label="查询时间">
                            </el-table-column>
                            <el-table-column
                                    prop="queryCount"
                                    label="查询条数">
                            </el-table-column>
                            <el-table-column
                                    prop="amount"
                                    label="余额">
                            </el-table-column>
                            <el-table-column
                                    prop="isReturn"
                                    label="是否返回结果">
                                <template slot-scope="scope">
                                        <span>{{scope.row.isReturn?'已返回':'未返回'}}</span>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>
                    <div class="paging">
                        <div class="block_plo">
                            <el-pagination
                                    @size-change="handleSizeChange"
                                    @current-change="handleCurrentChange"
                                    :current-page="currentPage"
                                    :prev-text="'上一页'"
                                    :next-text="'下一页'"
                                    :page-count="pageCount"
                                    :page-size="pagesize"
                                    layout="total, sizes, prev, pager, next, jumper"
                                    :total="pageTotal">
                            </el-pagination>
                        </div>
<!--                        <div class="export">-->
<!--                            <button class="export_ko">导出</button>-->
<!--                        </div>-->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import headerclass from "@views/Charging/header/header";
    import {statCostList,statCost} from "@/network/admin";
    export default {
        name: "stayls",
        data() {
            return {
                DataSelect: '',//日期
                tableData: [],
                loading: true,
                currentPage: 1,
                pagesize: 10,
                pageTotal: 0,
                pageCount: 0,
                queryTotal:''
            }
        },
        created: function () {
            this.hadleGetCustomerApi();
        },
        activated:function(){
            this.hadleGetCustomerApi();
        },
        components: {
            headerclass,
        },
        methods: {
            //分页
            handleSizeChange: function (size) {
                this.pagesize = size;
                this.hadleGetCustomerApi();
                var itemsPerPage = this.pagesize;
            },
            // 控制页面的切换
            handleCurrentChange: function (currentPage) {
                this.currentPage = currentPage;
                var currentPage = this.currentPage;
                this.hadleGetCustomerApi();
            },
            //请求列表
            hadleGetCustomerApi() {
                let custNumber = this.$route.query.custNumber;
                var data = {
                    custNumber: custNumber,
                    currentPage: this.currentPage,
                    itemsPerPage: this.pagesize
                };
                statCostList(data).then(res => {
                    // console.log(res.data.infos[0].custId);
                    this.tableData = res.data.costs;
                    this.pageTotal = res.data.total;
                    this.pageCount = res.data.pages;
                    this.queryTotal = res.data.queryTotal;
                })
                    .catch({});
            },
        //    查询
            checkpp(){
                if (this.DataSelect == '') {
                    this.$message({
                        message: '请选择时间',
                        type: 'warning'
                    });
                    return;
                }
                if(this.DataSelect == null) {
                    this.$message({
                        message: '请选择时间',
                        type: 'warning'
                    });
                    return;
                } else {
                    // --时间处理
                    const startDate = this.DataSelect[0];
                    const endDate = this.DataSelect[1];
                    let d = new Date(startDate);
                    d = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
                    let c = new Date(endDate);
                    c = c.getFullYear() + '-' + (c.getMonth() + 1) + '-' + c.getDate();
                    let custNumber = this.$route.query.custNumber;
                    const data = {
                        custNumber: custNumber,
                        currentPage: this.currentPage,
                        itemsPerPage: this.pagesize,
                        startDate: d,
                        endDate: c
                    };
                    statCost(data).then(res => {
                        // console.log(res.data.infos[0].custId);
                        this.tableData = res.data.costs;
                        this.pageTotal = res.data.total;
                        this.pageCount = res.data.pages;
                    })
                        .catch({});
                }


            },
        }
    }
</script>

<style>
    @import "~@assets/css/user/Statistical.css";
</style>